import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { map, flatMap } from 'rxjs/operators';

import { Company } from './company';
import { CompaniesService } from './companies.service';
import { PortalService } from '../portal/portal.service';

@Injectable()
export class CompanyWebResolver implements Resolve<string>  {
  constructor(private companiesService: CompaniesService, private sharepointService: PortalService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<string> {
    const id: number = route.params.id;
    return this.companiesService.getItems({ filter: `Id eq '${id}'`})
      .pipe(map((c: Company[]) =>  c.length > 0 ? c[0] : null ),
      flatMap((c: Company) => this.sharepointService.getWebIdByRelativeUrl(c && c.url)));
  }
}
