import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import * as moment from 'moment';

import { Incident } from 'src/app/services/incidents/incident';
import { IncidentsService } from 'src/app/services/incidents/incidents.service';

@Component({
  selector: 'app-incidents-informer',
  templateUrl: './incidents-informer.component.html',
  styleUrls: ['./incidents-informer.component.scss']
})
export class IncidentsInformerComponent implements OnInit {

  public counter: number = null;
  public incident: Incident = null;

  private webId: string = null;
  private subscription: Subscription = null;

  constructor(private route: ActivatedRoute,
    private incidentsService: IncidentsService) { }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
  }

  private load() {
    this.incidentsService.getItems({ top: 1, orderBy: [['slDate', false]], webId: this.webId})
      .subscribe((incidents: Incident[]) => {
        if (incidents.length > 0) {
          const today = moment();
          const date = moment(incidents[0].date);
          const counter = today.diff(date, 'days');
          if (counter > 10) {
            this.counter = counter;
          } else {
            this.incident = incidents[0];
          }
        } else {
          this.counter = null;
          this.incident = null;
        }
      });
  }

  getEnding(number: number, endings: string[]) {
    let ending: string;
    if (number) {
      number = number % 100;
      if (number >= 11 && number <= 19) {
        ending = endings[2];
      } else {
        const i = number % 10;
        switch (i) {
          case 1:
            ending = endings[0];
            break;
          case 2:
          case 3:
          case 4:
            ending = endings[1];
            break;
          default:
            ending = endings[2];
        }
      }
      ending = `${number} ${ending}`;
    } else {
      ending = `Нет ${endings[2]}`;
    }
    return ending;
  }
}
