import { Injectable } from '@angular/core';
import { ListItemsService } from '../list-items-service';
import { Incident } from './incident';
import { PortalService } from '../portal/portal.service';

@Injectable({
  providedIn: 'root'
})
export class IncidentsService extends ListItemsService<Incident> {

  constructor(private sharepointService: PortalService) {
    super();
  }

  getListTitle(): string {
    return 'Происшествия';
  }

  getListRelativePath(): string {
    return 'Lists/slIncidents';
  }

  getSelect(): string {
    return 'Id,Title,slDate';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): Incident {
    const entity: Incident = {
      id: item.Id,
      title: item.Title,
      date: new Date(item.slDate)
    };
    return entity;
  }

  convertEntityToListItem(entity: Incident) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Incident[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
