import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { Employee } from 'src/app/services/employees/employee';
import { Company } from 'src/app/services/companies/company';
import { EmployeesService } from 'src/app/services/employees/employees.service';

@Component({
  selector: 'app-enterprise-birthdays-informer',
  templateUrl: './enterprise-birthdays-informer.component.html',
  styleUrls: ['./enterprise-birthdays-informer.component.scss']
})
export class EnterpriseBirthdaysInformerComponent implements OnInit {
  public employees: Employee[] = null;
  public currentDay: number;
  public company: Company = null;

  private subscription: Subscription = null;

  constructor(private route: ActivatedRoute,
    private employeesService: EmployeesService) {
    this.currentDay = new Date().getDate();
  }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
        this.load();
      });
  }

  public load() {
    const year = new Date().getFullYear();
    const month = new Date().getMonth() + 1;
    const start = new Date().getDate();
    const finish = new Date(year, month, 0).getDate();
    this.employeesService.getItems({
      filter: `slBirthdayMonth eq ${month} and slBirthdayDay ge ${start} and slBirthdayDay le ${finish}`
        + ` and slCompany eq '${this.company.code}'`,
      orderBy: [['slBirthdayDay', true], ['slFullName', true]],
      top: 5000
    }).subscribe(birthdays => this.employees = birthdays);
  }
}
