import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { map } from 'rxjs/internal/operators/map';
import { Subscription } from 'rxjs';

import { Fame } from 'src/app/services/fames/fame';
import { FamesService } from 'src/app/services/fames/fames.service';

@Component({
  selector: 'app-enterprise-hall-of-fame-informer',
  templateUrl: './enterprise-hall-of-fame-informer.component.html',
  styleUrls: ['./enterprise-hall-of-fame-informer.component.scss']
})
export class EnterpriseHallOfFameInformerComponent implements OnInit {

  public employees: Fame[] = null;

  private webId: string = null;
  private subscription: Subscription = null;

  constructor(private route: ActivatedRoute,
    private famesService: FamesService) { }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
  }

  private load() {
    this.famesService.getItems({ top: 3, orderBy: [['Created', false]], webId: this.webId})
      .subscribe((employees: Fame[]) => this.employees = employees);
  }
}
