import { Injectable } from '@angular/core';
import { Event } from './event';
import { ListItemsService } from '../list-items-service';

@Injectable({
  providedIn: 'root'
})
export class EventsService extends ListItemsService<Event>  {

  getListTitle(): string {
    return 'События';
  }

  getListRelativePath(): string {
    return 'Lists/slEvents';
  }

  getSelect(): string {
    return 'Id,Title,slEventsCover';
  }

  getExpand(): string {
    return '';
  }

  getOrderBy(): [string, boolean][] {
    return [];
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): Event {
    const entity: Event = {
      id: item.Id,
      title: item.Title,
      coverUrl: item.slEventsCover
    };
    return entity;
  }

  convertEntityToListItem(entity: Event) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Event[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
