import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { Company } from 'src/app/services/companies/company';
import { PortalService } from 'src/app/services/portal/portal.service';
import { EventsService } from 'src/app/services/events/events.service';
import { Event } from 'src/app/services/events/event';

@Component({
  selector: 'app-doings-informer',
  templateUrl: './doings-informer.component.html',
  styleUrls: ['./doings-informer.component.scss']
})
export class DoingsInformerComponent implements OnInit {

  public events: Event[] = [];

  public carouselIsUp: Boolean = false;
  public currentSlide = 0;
  private webId: string = null;
  private company: Company = null;
  private subscription: Subscription = null;

  constructor(private route: ActivatedRoute,
    private sharepointService: PortalService,
    private eventsService: EventsService) { }

  ngOnInit() {
    this.subscription = this.route.data
      .pipe(map(data => data.webId))
      .subscribe(webId => {
        this.webId = webId;
        this.load();
      });
    this.subscription = this.route.data
      .pipe(map(data => data.company))
      .subscribe(company => {
        this.company = company;
      });
  }

  private load() {
    this.eventsService.getItems({ top: 3, orderBy: [['Created', false]], webId: this.webId})
      .subscribe((events: Event[]) => this.events = events);
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slEvents/NewForm.aspx?Source=`
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `${this.company.url}/Lists/slEvents/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.eventsService.recycleItem(item, this.webId)
      .subscribe(() => this.load());
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)') || null;
  }

}
